}

.view-more:hover {
  color: white;
}

.media-studio-shiny-text {
  background: linear-gradient(120deg, #d946ef 0%, #a855f7 25%, #3b82f6 50%, #a855f7 75%, #d946ef 100%);
  background-size: 200% 100%;
  -webkit-background-clip: text;
